.demo_item {
  padding: 20px;
  border-radius: 20px;
  background: #ffffff;
  box-shadow: 0px 17px 13px 0px #192c4b05;
  position: relative;
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0.1s;
  transform: translateY(0);
  display: block;
  transform-origin: 50% -50px;
  margin-bottom: 40px;
  transform: perspective(300px) rotateX(-3deg);
  position: relative;
  position: relative;
}

.demo_item.is_sm .img_wrap {
  width: 100%;
  height: 10rem;
  border-radius: 20px;
  overflow: hidden;
}

@media screen and (max-width: 991px) {
  .demo_item.is_sm .img_wrap {
    height: 16rem;
  }
}

.demo_item.is_lg {
  transform: none !important;
}

.demo_item.is_lg .img_wrap {
  width: 100%;
  height: 26.5rem;
  border-radius: 20px;
  overflow: hidden;
}

.demo_item::before {
  content: "";
  position: absolute;
  width: 90%;
  height: 20px;
  left: 50%;
  top: -20px;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.514);
  border-radius: 20px 20px 0 0;
  z-index: -1;
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.demo_item::after {
  content: "";
  position: absolute;
  width: 80%;
  height: 40px;
  left: 50%;
  top: -40px;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.26);
  border-radius: 20px 20px 0 0;
  z-index: -2;
  transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0.05s;
}

.demo_item .img_wrap {
  width: 100%;
  height: 20rem;
  border-radius: 20px;
  overflow: hidden;
}

.demo_item .img_wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  object-position: top center;
}

.demo_item:hover {
  transform: translateY(-0.5rem);
  position: relative;
  position: relative;
}

.demo_item:hover .img_wrap img {
  transform: scale(1.05);
}

.demo_item:hover::before {
  content: "";
  position: absolute;
  top: 0;
}

.demo_item:hover::after {
  content: "";
  position: absolute;
  top: 0px;
}

